<template>
    <main>
        <a-cell>
            <template slot="prepend"> </template>
            <a-input ref="inputSchool" :vaildate-rules="vaildates" placeholder="" v-model="text1"/>
        </a-cell>

        <a-cell>
            <template slot="prepend"> </template>
            <a-input :is-select-all="true" maxlength="13" v-model="text3" type="phone" />
        </a-cell>

        <a-cell>
            <template slot="prepend">  </template>
            <a-input maxlength="13" v-model="text4" type="number" />
        </a-cell>
        
        <a-cell>
            <template slot="prepend"></template>
            <a-input maxlength="14" v-model="text2" type="bankCode" />
        </a-cell>
        
        <a-cell>
            <template slot="prepend"></template>
            <a-input maxlength="13" v-model="text5" type="letter" />
        </a-cell>
        
        <a-cell>
            <template slot="prepend"></template>
            <a-input :filter="/[^a-zA-Z]/g" v-model="text6"/>
        </a-cell>

        <div class="fill">
            <a-button @click="validate"></a-button>
        </div>
        
    </main>
</template>
<script>
export default {
    name: 'InputDemo',

    data() {
        return {
            text1: 'hello vue !',
            text2: '0000 0000 0000 0000',
            text3: '133123456789',
            text4: '01234567',
            text5: 'abc',
            text6: 'abc',
            isShowWarning: true,
            vaildates: [
                { required: true, message: '!' },
                {trigger:'keyup', message: '100', fn: this.isSame }, 
                { test: /a/, message: 'a!' },{ test: /b/, message: 'b!' },
            ]
        };
    },

    methods: {
        validate() {
            let result = this.$refs.inputSchool.validate();
            this.$toast(`: ${result}`);
        },

        isSame() {
            return 100 != this.text1;
        }
    }
};
</script>
<style scoped lang="scss">
</style>



